'use client';

import { useState, useRef, useEffect } from 'react';
import { Send, User, Bot, Loader2, MessageSquare } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Message } from '@/types/chat';
import { useChat } from '@/contexts/ChatContext';
import ReactMarkdown from 'react-markdown';
import { motion, AnimatePresence } from 'framer-motion';
import { cn } from '@/lib/utils';
import { ChatInput } from './chat-input';
import { MessageItem } from './message-item';

interface ChatWindowProps {
  messages: Message[];
  isLoading: boolean;
  onSendMessage: (content: string) => Promise<void>;
  isReady?: boolean;
}

const messageVariants = {
  hidden: { opacity: 0, y: 20 },
  visible: { opacity: 1, y: 0 },
  exit: { opacity: 0, x: -100 }
};

export function ChatWindow({ messages, isLoading, onSendMessage, isReady = true }: ChatWindowProps) {
  const [input, setInput] = useState('');
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const { streamingContent } = useChat();

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages, streamingContent]);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!input.trim() || isLoading) return;
    onSendMessage(input);
    setInput('');
  };

  return (
    <div className="flex flex-col h-full bg-gradient-to-b from-[#0A0A0A] via-[#0A0A0A] to-[#141414] rounded-3xl overflow-hidden shadow-2xl">
      {/* Header */}
      <div className="flex-none bg-gradient-to-b from-[#0A0A0A] to-transparent px-6 py-4">
        <div className="flex items-center justify-between">
          <div className="flex items-center gap-3">
            <div className="w-9 h-9 rounded-xl bg-gradient-to-br from-amber-500/10 to-yellow-500/10 flex items-center justify-center ring-1 ring-amber-500/20">
              <Bot className="w-5 h-5 text-amber-400" />
            </div>
            <div>
              <h2 className="text-sm font-medium text-amber-200/80">AI Assistant</h2>
              <p className="text-xs text-gray-500">Powered by Claude</p>
            </div>
          </div>
          {isLoading && (
            <div className="flex items-center gap-2 px-3 py-1.5 rounded-lg bg-[#161616]/50 ring-1 ring-amber-500/10">
              <Loader2 className="w-3.5 h-3.5 text-amber-400 animate-spin" />
              <span className="text-xs font-medium text-amber-200/70">Processing...</span>
            </div>
          )}
        </div>
      </div>

      {/* Messages Area */}
      <div className="flex-1 overflow-y-auto scrollbar-thin scrollbar-thumb-amber-600/10 scrollbar-track-transparent">
        <div className="max-w-3xl mx-auto px-4 py-6 space-y-6">
          {messages.length === 0 && !isLoading && (
            <div className="h-[300px] flex flex-col items-center justify-center text-center p-4">
              <div className="w-16 h-16 rounded-2xl bg-gradient-to-br from-amber-500/10 to-yellow-500/10 flex items-center justify-center mb-4 ring-1 ring-amber-500/20">
                <MessageSquare className="w-8 h-8 text-amber-400" />
              </div>
              <p className="text-sm font-medium text-amber-200/70">No messages yet</p>
              <p className="text-xs text-gray-500 mt-1">Start a conversation to begin</p>
            </div>
          )}

          <AnimatePresence initial={false}>
            {messages.map((message, index) => (
              <motion.div
                key={index}
                variants={messageVariants}
                initial="hidden"
                animate="visible"
                exit="exit"
                transition={{ duration: 0.3 }}
                className="group"
              >
                <div className="flex items-start gap-4 rounded-2xl p-4 hover:bg-[#161616]/50 transition-all duration-300">
                  <div className={cn(
                    "w-9 h-9 rounded-xl flex items-center justify-center flex-shrink-0 transition-all duration-300 ring-1",
                    message.role === 'assistant' 
                      ? "bg-gradient-to-br from-amber-500/10 to-yellow-500/10 text-amber-400 ring-amber-500/20" 
                      : "bg-gradient-to-br from-amber-400/5 to-yellow-400/5 text-amber-300 ring-amber-400/10"
                  )}>
                    {message.role === 'assistant' ? (
                      <Bot className="w-5 h-5" />
                    ) : (
                      <User className="w-5 h-5" />
                    )}
                  </div>
                  <div className="flex-1 space-y-2 overflow-hidden">
                    <div className="text-sm font-medium text-amber-200/80">
                      {message.role === 'user' ? 'You' : 'Assistant'}
                    </div>
                    <div className="text-gray-100">
                      <div className="prose prose-invert max-w-none break-words prose-p:leading-relaxed prose-p:text-gray-100 prose-p:my-1 prose-headings:text-amber-100 prose-strong:text-amber-200 prose-ul:text-gray-100 prose-ol:text-gray-100 prose-li:text-gray-100 prose-pre:bg-[#161616]/80 prose-pre:text-sm prose-code:text-amber-200 prose-a:text-amber-400">
                        <ReactMarkdown>{message.content}</ReactMarkdown>
                      </div>
                    </div>
                  </div>
                </div>
              </motion.div>
            ))}
          </AnimatePresence>

          {/* Streaming Message */}
          <AnimatePresence>
            {isLoading && streamingContent && (
              <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0 }}
                className="group"
              >
                <div className="flex items-start gap-4 rounded-2xl p-4 bg-[#161616]/50">
                  <div className="w-9 h-9 rounded-xl bg-gradient-to-br from-amber-500/10 to-yellow-500/10 text-amber-400 flex items-center justify-center flex-shrink-0 ring-1 ring-amber-500/20">
                    <Bot className="w-5 h-5" />
                  </div>
                  <div className="flex-1 space-y-2 overflow-hidden">
                    <div className="text-sm font-medium text-amber-200/80">Assistant</div>
                    <div className="text-gray-100">
                      <div className="prose prose-invert max-w-none break-words prose-p:leading-relaxed prose-p:text-gray-100 prose-p:my-1 prose-headings:text-amber-100 prose-strong:text-amber-200 prose-ul:text-gray-100 prose-ol:text-gray-100 prose-li:text-gray-100 prose-pre:bg-[#161616]/80 prose-pre:text-sm prose-code:text-amber-200 prose-a:text-amber-400">
                        <ReactMarkdown>{streamingContent}</ReactMarkdown>
                      </div>
                    </div>
                  </div>
                </div>
              </motion.div>
            )}
          </AnimatePresence>

          {/* Loading Indicator */}
          <AnimatePresence>
            {isLoading && !streamingContent && (
              <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0 }}
                className="group"
              >
                <div className="flex items-start gap-4 rounded-2xl p-4">
                  <div className="w-9 h-9 rounded-xl bg-gradient-to-br from-amber-500/10 to-yellow-500/10 text-amber-400 flex items-center justify-center flex-shrink-0 ring-1 ring-amber-500/20">
                    <Loader2 className="w-5 h-5 animate-spin" />
                  </div>
                  <div className="flex-1 space-y-2">
                    <div className="text-sm font-medium text-amber-200/80">Assistant</div>
                    <div className="flex items-center gap-2">
                      <div className="w-2 h-2 bg-amber-400/30 rounded-full animate-bounce" />
                      <div className="w-2 h-2 bg-amber-400/30 rounded-full animate-bounce delay-150" />
                      <div className="w-2 h-2 bg-amber-400/30 rounded-full animate-bounce delay-300" />
                    </div>
                  </div>
                </div>
              </motion.div>
            )}
          </AnimatePresence>

          <div ref={messagesEndRef} />
        </div>
      </div>

      {/* Input Area */}
      <div className="flex-none bg-gradient-to-t from-[#0A0A0A] to-transparent backdrop-blur-xl pt-6">
        <ChatInput 
          onSendMessage={onSendMessage} 
          isLoading={isLoading} 
          isReady={isReady}
        />
      </div>
    </div>
  );
} 