// component for the chat interface

'use client';

import { useState } from 'react';
import { ChatWindow } from './chat-window';
import { ChatThread } from './chat-thread';
import { useThread } from '@/contexts/ThreadContext';
import { useChat } from '@/contexts/ChatContext';
import { Settings, X, ChevronLeft, Bug } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { cn } from '@/lib/utils';
import { motion } from 'framer-motion';

interface ChatProps {
  onShowDebug?: () => void;
}

export function Chat({ onShowDebug }: ChatProps) {
  const { threads, activeThreadId, setActiveThreadId, createThread } = useThread();
  const { messages, isLoading, sendMessage } = useChat();
  const [isSidebarOpen, setIsSidebarOpen] = useState(true);
  const activeThread = threads.find(t => t.thread_id === activeThreadId);

  return (
    <div className="relative flex h-full">
      {/* Sidebar */}
      <div className={cn(
        "absolute inset-y-0 left-0 z-20 transition-transform duration-300 lg:relative lg:transition-none",
        isSidebarOpen ? "translate-x-0" : "-translate-x-full lg:translate-x-0"
      )}>
        <ChatThread
          threads={threads}
          onNewChat={createThread}
          onSelectThread={setActiveThreadId}
          activeThreadId={activeThreadId}
        />
      </div>

      {/* Main Chat Area */}
      <div className="flex-1 flex flex-col min-w-0">
        {/* Top Navigation */}
        <div className="flex-none px-4 py-3 bg-gradient-to-b from-[#0A0A0A] to-transparent">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-3">
              {/* Mobile Sidebar Toggle */}
              <Button
                variant="ghost"
                size="icon"
                onClick={() => setIsSidebarOpen(!isSidebarOpen)}
                className="lg:hidden hover:bg-[#161616]/50 text-amber-200/70"
              >
                {isSidebarOpen ? (
                  <X className="h-5 w-5" />
                ) : (
                  <ChevronLeft className="h-5 w-5" />
                )}
              </Button>

              {/* Thread Title */}
              <div className="flex items-center">
                <h1 className="text-lg font-semibold bg-gradient-to-r from-amber-200 to-yellow-200 bg-clip-text text-transparent">
                  {activeThread?.title || 'New Chat'}
                </h1>
              </div>
            </div>

            {/* Right Actions */}
            <div className="flex items-center gap-2">
              <motion.div whileHover={{ scale: 1.05 }} whileTap={{ scale: 0.95 }}>
                <Button
                  onClick={onShowDebug}
                  size="icon"
                  className="bg-gradient-to-br from-amber-500/10 to-yellow-500/10 hover:from-amber-500/20 hover:to-yellow-500/20 text-amber-400 ring-1 ring-amber-500/20"
                >
                  <Bug className="h-4 w-4" />
                </Button>
              </motion.div>
            </div>
          </div>
        </div>

        {/* Chat Window */}
        <div className="flex-1 min-h-0 p-4">
          <ChatWindow
            messages={messages}
            isLoading={isLoading}
            onSendMessage={sendMessage}
            isReady={!!activeThreadId}
          />
        </div>
      </div>
    </div>
  );
} 